@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
html , body , *{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}
.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.selectedVideo{
    width: 100%;
}

.selected-video{
    width: 829px;
    height: 466px;
}

@media only screen and (max-width: 900px)
{
    .selected-video{
        width: 100vw;
        height: 40vh;
    }
}
.container-searchbar{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em;
    width: 82vw;
}

.search-input{
    height: 2rem;
    width: 100%;
    padding: 0.4em;
    border: solid 1px #777;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    outline-style: none;
}

.search-btn{
    height: 2rem;
    align-items: center;
    padding: 0 1.25rem;
    border: 1px solid #777;
    background-color: #f8f8f8;
    border-left: none;
    cursor: pointer;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.container-videolist img{
    max-width: 829px;
    max-height: 466px;
}
